
import { Component, OnInit } from '@angular/core';
import { LoadingController } from '@ionic/angular';
import { AuthService } from 'src/app/services/auth/auth.service';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ToastService } from 'src/app/services/toast/toast.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-send',
  templateUrl: './send.page.html',
  styleUrls: ['./send.page.scss'],
})
export class SendPage implements OnInit {

  current_year: number = new Date().getFullYear();

  send_fund_form: UntypedFormGroup;
  submit_attempt: boolean = false;

  constructor(
    private authService: AuthService,
    private loadingController: LoadingController,
    private formBuilder: UntypedFormBuilder,
    private toastService: ToastService,
    private router: Router
  ) { }

  ngOnInit() {

    // Setup form
    this.send_fund_form = this.formBuilder.group({
      address: [''  ],
      amount: [''  ]
    });


  }

  // Sign in
  async send_fund() {

    this.submit_attempt = true;

    // If email or password empty
    if (this.send_fund_form.value.address == '' || this.send_fund_form.value.amount == '') {
      this.toastService.presentToast('Error', 'Please input address and amount', 'top', 'danger', 2000);

    } else {

      // Proceed with loading overlay
      const loading = await this.loadingController.create({
        cssClass: 'default-loading',
        message: 'Signing in... Please be patient.',
        spinner: 'crescent'
      });
      await loading.present();


      this.authService
        .send_fund(this.send_fund_form.value)
        .subscribe((data) => {

          this.toastService.presentToast('Success', 'Request forwarded successfully', 'top', 'success', 2000);

        

          loading.dismiss();

        });


    }
  }



}
